.directory {
    /* background: #242322; */
    font-family: monospace;
    font-size: 16px;
    /* color: rgb(0, 0, 0); */
    user-select: none;
  }
  
  .directory .tree,
  .directory .tree-node,
  .directory .tree-node-group {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .directory .tree-branch-wrapper,
  .directory .tree-node__leaf {
    outline: none;
    outline: none;
  }
  
  .directory .tree-node {
    cursor: pointer;
  }
  
  .directory .tree-node:hover {
    background: rgba(0, 0, 0, 0.1);
  }
  
  .directory .tree .tree-node--focused {
    background: rgba(0, 0, 0, 0.2);
  }
  
  .directory .tree .tree-node--selected {
    background: rgb(139, 186, 240);
  }
  
  .directory .tree-node__branch {
    display: block;
  }
  
  .directory .icon {
    vertical-align: middle;
    padding-right: 5px;
    font-size: 24px;
  }